import React from "react";
import PropTypes from "prop-types";
import "./Movie.css";

function Movies({ year, title, summary, poster, genres }) {
  return (
    <div className="movie">
      <img src={poster} alt={title} title={title} />
      <div className="movie__data">
        <h3 className="movie__title" style={{bakgroundColor: "yellow"}}>{title}</h3>
        <h5 className="movie__year">{year}</h5>
        <ul className="movie__genres">{genres.map((genre, index) => (
            <li key={index} className="genres__genre">{genre}</li>
        ))}</ul>
        <p className="movie__summary">{summary.slice(0,180)}...</p>
        
      </div>
    </div>
  );
}

Movies.propTypes = {
  id: PropTypes.number.isRequired, //P가 대문자로 시작하는 것 주의
  year: PropTypes.number.isRequired,
  title: PropTypes.string.isRequired,
  summary: PropTypes.string.isRequired,
  poster: PropTypes.string.isRequired, //medium_cover_image 대신 사용하는 이름
  genres: PropTypes.arrayOf(PropTypes.string).isRequired
};

export default Movies;
